/* 
green: #3FAF82

*/
.hamburguer {
  display: none;
}

.btn {
  // position: absolute;
  // top: 50%;
  // left: 50%;
  // transform: translate(-50%, -50%);
  width: 3.5rem;
  cursor: pointer;
}

span {
  display: block;
  width: 100%;
  // box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
  border-radius: 3px;
  height: 2px;
  background: #4d4f4fc3;
  transition: all 0.3s;
  position: relative;
}

span + span {
  margin-top: 0.7rem; // espa√ßamento entre as barras
}

.true span:nth-child(1) {
  animation: ease 0.5s top forwards;
}

.false span:nth-child(1) {
  animation: ease 0.5s top-2 forwards;
}

.true span:nth-child(2) {
  animation: ease 0.5s scaled forwards;
}

.false span:nth-child(2) {
  animation: ease 0.5s scaled-2 forwards;
}

.true span:nth-child(3) {
  animation: ease 0.5s bottom forwards;
}

.false span:nth-child(3) {
  animation: ease 0.5s bottom-2 forwards;
}

@keyframes top {
  0% {
    top: 0;
    transform: rotate(0);
  }
  50% {
    top: 9px;
    transform: rotate(0);
  }
  100% {
    top: 9px;
    transform: rotate(45deg);
  }
}

@keyframes top-2 {
  0% {
    top: 9px;
    transform: rotate(45deg);
  }
  50% {
    top: 9px;
    transform: rotate(0deg);
  }
  100% {
    top: 0;
    transform: rotate(0deg);
  }
}

@keyframes bottom {
  0% {
    bottom: 0;
    transform: rotate(0);
  }
  50% {
    bottom: 9px;
    transform: rotate(0);
  }
  100% {
    bottom: 9px;
    transform: rotate(135deg);
  }
}

@keyframes bottom-2 {
  0% {
    bottom: 9px;
    transform: rotate(135deg);
  }
  50% {
    bottom: 9px;
    transform: rotate(0);
  }
  100% {
    bottom: 0;
    transform: rotate(0);
  }
}

@keyframes scaled {
  50% {
    transform: scale(0);
  }
  100% {
    transform: scale(0);
  }
}

@keyframes scaled-2 {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@media (max-width: 821px) {
  .hamburguer {
    display: block;
  }
}
